@import url(https://fonts.googleapis.com/css?family=Baloo|Caveat|Kaushan+Script|McLaren|Satisfy&display=swap);
@-webkit-keyframes asdf {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes asdf {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
#root {
  height: 100vh;
}

body {
  height: 100%;
  justify-content: center;
  align-content: center;
  margin: 0;
  padding: 0;
  font-family: "Baloo", cursive;
  overflow: hidden;
}

.haha {
  height: 100%;
}
.haha > button {
  color: white;
  background: transparent;
  font-size: 3em;
  font-family: "Baloo", cursive;
  display: flex;
  position: fixed;
  padding: 0.2em 1.4em;
  margin: 0 auto;
  border: none;
  outline: none;
  left: 45%;
  bottom: 0;
  cursor: pointer;
}
.haha .contact {
  position: absolute;
  z-index: 100;
  height: 50%;
  width: 50%;
  top: 25%;
  left: 25%;
  background: white;
  opacity: 1;
  border-radius: 40px;
  box-shadow: 0 0 0 1600px rgba(0, 0, 0, 0.6);
  -webkit-transition: opacity 1s;
  transition: opacity 1s;
}
.haha .contact.hidden {
  visibility: hidden;
  opacity: 0;
}
.haha .contact > i {
  position: absolute;
  cursor: pointer;
  right: 4%;
  top: 4%;
  opacity: 0.7;
  font-size: 1.5em;
}
.haha .contact > div {
  position: relative;
  left: 25%;
  top: 25%;
  margin: 20px;
  cursor: pointer;
}
.haha .contact > div > i {
  font-size: 2.5em;
}
.haha .contact > div > a {
  font-size: 1.7em;
  text-decoration: none;
  color: black;
  padding: 15px;
}
.haha .contact > div:hover {
  opacity: 0.4;
}

.scrollBar {
  position: fixed;
  color: white;
  top: 50%;
  right: 2%;
  list-style-type: none;
  margin: auto 0;
  padding: 0;
  opacity: 0.7;
}

.Container {
  height: 100%;
  display: flex;
  background-size: cover;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.45)), to(rgba(0, 0, 0, 0.45))), url("https://i.imgur.com/uow5IDn.jpg");
  background-image: linear-gradient(rgba(0, 0, 0, 0.45), rgba(0, 0, 0, 0.45)), url("https://i.imgur.com/uow5IDn.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  -webkit-transition: 1.5s ease;
  transition: 1.5s ease;
}
.Container .a {
  margin-top: 10%;
  height: 70%;
  width: 100%;
}
.Container .a .Heading {
  display: flex;
  position: relative;
  font-size: 3em;
  width: 90%;
  left: 10%;
  margin: 1em 0;
  color: white;
}
.Container .a .Content {
  display: flex;
  position: relative;
  font-size: 2em;
  left: 10%;
  width: 80%;
  color: white;
}

.Container2 {
  height: 100%;
  display: flex;
  position: relative;
  background-size: cover;
  background-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.4)), to(rgba(0, 0, 0, 0.4))), url("https://i.imgur.com/718Un17.jpg");
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("https://i.imgur.com/718Un17.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
}
.Container2 .Content {
  position: relative;
  display: flex;
  font-size: 2em;
  color: white;
  top: 30%;
  left: 10%;
  letter-spacing: 0.05em;
  height: 300px;
}

.title {
  position: absolute;
  color: white;
  font-size: 2.5em;
  margin: 7%;
}

.navBar {
  position: fixed;
  top: 0px;
  color: white;
  width: 100%;
  font-family: "Baloo", cursive;
}
.navBar > ul {
  list-style-type: none;
  font-size: 1.5em;
  float: right;
  margin: 20px;
}
.navBar > ul > li {
  display: inline;
  margin: 0 30px;
  cursor: pointer;
}
